.pile
{
    position: absolute;
    border-width: 3px;
    border-style: dashed;
    border-color: rgba(255, 255, 255, 0.05);
    background: rgba(255, 255, 255, 0.04);
    /* width: 200px;
    height: 290px; */
    border-radius: 2em;
    transition: border-color 0.1s, background 0.3s;
}

.pile.dragging
{
    z-index: 1000;
}

.pile.dragging,
.pile:hover
{
    border-color: rgba(255, 255, 255, 0.1);
    background: rgba(255, 255, 255, 0.08);
}
