.card
{
    position: absolute;
    z-index: 100;
    cursor: grab;
    touch-action: none;
}

.card-graphics
{
    padding: 0.25em;
    border-radius: 1em;
    border-width: 1px;
    border-style: solid;
    border-color: var(--border-dark);
    background: rgb(245, 230, 210);
    box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    text-align: center;
}

.card-graphics .flip
{
    transform: rotate(180deg);
}

.card-graphics .fill
{
    height: 100%;
    width: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.card.disabled
{
    cursor: not-allowed;
}

.card.dragging
{
    z-index: 1000;
    cursor: grabbing;
}

.card.dragging .card-graphics
{
    box-shadow: 2px 2px 3px 3px rgba(0, 0, 0, 0.08);
    text-align: center;
}

.card .card-graphics,
.card .card-graphics > *,
.card .card-graphics > .front > *
{
    height: 100%;
}

.card .card-graphics .back
{
    background-color: var(--back-color);
    border-radius: 0.75em;

    /* opacity: 0.8; */
    background-image:
        repeating-radial-gradient(
            circle at 0 0,
            transparent 0,
            var(--back-color) 10px
        ),
        repeating-linear-gradient(
            var(--back-pattern-color-2),
            var(--back-pattern-color)
        );
}

.card .card-graphics .front
{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform: rotate3d(0, 1, 0, 180deg);
}

.card-graphics .front .corner
{
    position: absolute;
    display: flex;
    flex-flow: column;
    align-items: center;
    font-size: 1.5rem;
    line-height: 0.9em;
    padding-top: 0.25em;
    padding-left: 0.1em;
}

.card-graphics .front .corner.top
{
    top: 0;
}

.card-graphics .front .corner.bottom
{
    bottom: 0;
}

.card-graphics .front .corner.right
{
    right: 0;
}

.card-graphics .front .face
{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    font-size: 3em;
    line-height: 0.5em;
}
